import React, { Component } from 'react';

import '../App.css';

import AddField from './AddField';
import TodoList from './TodoList';
import TodoArchive from './TodoArchive';
import TodoActive from './TodoActive';
import TodoAll from './TodoAll';
import Counter from './Counter'

export default class TodoTable extends Component {
	state = {
		todos: [
			{ id: 0, task: 'Buy groceries', completed: false, editMode: false },
			{ id: 1, task: 'Take out trash', completed: false, editMode: false } 
		],
		editMode: false,
		newTodo: '',
	}

	onPressInput = (event) => {
		if (event.key === 'Enter') {
			this.handleOnItemAdded()
		} 
	}

	changeEditMode = (itemId) => {
		this.setState(({ todos }) => {
			let todosList = [...todos];

			for (let todo of todosList) {
				todo.editMode = false;
			}
			let index =  todosList.findIndex(item => item.id === itemId);
			todosList[index].editMode = !todosList[index].editMode;

			return {
				todos: todosList,
				editMode: !todosList.editMode,
			}
		}) 
	}

	updateItem = (event, itemId) => {
		let updatedItem = event.target.value;
		if (!updatedItem) {
			return
		}

		if (event.key === 'Enter') {
			this.setState(({ todos }) => {
				let todosList = [...todos];
				let index =  todosList.findIndex(item => item.id === itemId);
				todosList[index].task = updatedItem;
				todosList[index].editMode = false
				return {
					todos: todosList,
					editMode: false,
				}
			})
		}
	}

	cancelUpdate = (event, itemId) => {
		if (event.key === 'Escape') {
			this.setState(({ todos }) => {
				let todosList = [...todos];
				let index =  todosList.findIndex(item => item.id === itemId);
				todosList[index].editMode = false;
				return {
					todos: todosList,
					editMode: false,
				}
			})
		}
	}

	handleArchive = () => {
		this.setState(({ todos }) => {
			let remainingTasks = todos.filter(item => !item.completed);
			return {
				todos: remainingTasks,
			}
		})
	}

	showActive = () => {
		this.setState(({ todos }) => {
			let activeTasks = todos.filter(item => !item.completed)
			return {
				todos: activeTasks,
			}
	})
	}

	showAll = () => {
		this.setState(( { todos }) => {
			let allTasks = todos.map(item => item)
			return {
				todos: allTasks,
			}
		})
	} 

	handleItemChange = (text) => {
		this.setState( {
			newTodo: text,
		})
	}

	handleOnItemAdded = () => {

		if (!this.state.newTodo) {
			return 
		}

		this.setState(({ todos, newTodo }) => {
			let newItem = {
				id: todos.length + 1,
				task: newTodo,
				completed: false,
				editMode: false,
			}
	
			return {
				todos: [ newItem, ...todos ],
				newTodo: '',
			}
		})
	}

	handleItemCompleted = (itemId) => {
		this.setState(({ todos }) => {
			let todosList = [...todos];
			let index =  todosList.findIndex(item => item.id === itemId);
			todosList[index].completed = !todosList[index].completed;
			return {
				todos: todosList,
			}  
		})		
	}

	findIndex = (todos, itemId) => {
		let todosList = [...todos];
		return todosList.findIndex(item => item.id === itemId);
	}

  render() {
		const { todos, newTodo, editMode } = this.state;
		console.log(editMode)
    return (
			<div>
				<h1>Your todo list</h1>
				<AddField 
					onItemAdded={this.handleOnItemAdded}
					onItemChange={this.handleItemChange}
					onClearInput={newTodo}
					onPressInput={this.onPressInput}
				/>				
				<TodoList 
					todos={todos} 
					handleItemCompleted={this.handleItemCompleted}
					changeEditMode={this.changeEditMode}
					updateItem={this.updateItem}
					cancelUpdate={this.cancelUpdate}
				/>
				<TodoAll 
					showAll={this.showAll}
				/>
				<TodoArchive 
					handleArchive={this.handleArchive} 
				/>
				<TodoActive 
					showActive={this.showActive}
				/>
				<Counter 
					todos={todos}
				/> 
				<div className="App__todo-tips">
				{ editMode ? <div> 
					<p>Press 'Esc' to cancel changes</p>
					<p>Press 'Enter' to update the item</p>
				</div> : <div> Doubleclick to change the item </div> }
				</div>


			</div>
    )
  }
}